.positives {
  min-height: 68.75vw;
  display: flex;
  align-items: center;
  @include cover;
  background-image: url(./../img/promises.png);
  color: $white;
  text-align: center;
  @include lg {
    min-height: inherit; }
  @include sm {
    text-align: left; }
  &__inner {
    padding-top: 300px;
    padding-bottom: 300px;
    @include md {
      padding-top: 180px;
      padding-bottom: 180px; } }
  &__title {
    margin-bottom: 30px;
    @include sm {
      margin-bottom: 20px; } }
  &__descr {
    margin-bottom: 54px;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.4;
    @include sm {
      font-size: 16px;
      margin-bottom: 24px; } } }
